body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

img {
  width: 380px;
  height: 280px;
  object-fit: fill;
}

.container {
  max-width: 100vw;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  display: grid;
}

.photo-card {
  text-align: center;
  border: 1px solid #ccc;
  padding: 10px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

